@import url(http://fonts.googleapis.com/css?family=Montserrat);
@import "./layout";
@import "./card";
%zmp {
  margin-block-start: 0px;
  margin-block-end: 0px;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding: 0px;
}

%noSelect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

body,
html {
  @extend %zmp;
  font-family: "Montserrat";
}


ul,
p,
a,
li {
  @extend %zmp;
}

a {
  color: inherit; /* blue colors for links too */
  text-decoration: inherit; /* no underline */
}
.home-wrapper {
  width: 100vw;
  height: 100vh;
  background: url(../img/back.png);
  overflow: hidden;
  .container {
    height: 100%;
    display: flex;
    flex-direction: column;
    ul {
      list-style: none;
      display: flex;
      float: right;
      height: 100%;
      align-items: flex-end;
      li {
        @extend %noSelect;
        cursor: pointer;
        font-family: "Montserrat";
        color: #fff;
      }
    }

    .t-menu {
      height: 60px;
      margin: 0px 26.5px;
      padding-right: 5vw;
      border-bottom: 1px solid #d4938b;
      li {
        margin-left: 21px;
        margin-bottom: 12.5px;
      }
    }
    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      height: calc(100vh - 120px);
      .img {
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 1;
        height: 100%;
        img {
          width: 65%;
        }
      }
      .c-login {
        display: flex;
        flex-direction: column;
        align-items: center;
        background: rgba(14, 17, 33, 0.7);
        height: 80%;
        width: 25%;
        overflow: auto;
        // opacity: 0.7;
        border-radius: 10px;
        img {
          margin-top: 21px;
          width: calc(100% - 102px);
        }
        p.title {
          color: #fff;
          font-family: "Montserrat";
          font-size: 22px;
          margin-top: 23px;
          margin-bottom: 33px;
        }
      }

      form {
        display: flex;
        flex-direction: column;
        width: 65%;
        input[type="text"],
        input[type="password"] {
          background: transparent;
          border: none;
          border-bottom: 1px solid #d4938b;
          margin-bottom: 40px;
          padding: 6.5px 1px;
        }
        p {
          color: #fff;
          /* float: right; */
          text-align: right;
          font-size: 12px;
          margin-top: -35px;
        }

        .button {
          width: 60%;
          align-self: center;
          margin-top: 40px;
          color: #212543;
          background: #d4938b;
          background: linear-gradient(
            171deg,
            #d4938b 0%,
            #d9a9a4 26.11%,
            #debebb 50.76%,
            #e2ccca 60.13%,
            #e8e3e3 81.36%,
            #e9e9e9 100%
          );

          border-radius: 21px;
          border-width: 5px;
          height: 41px;
          border: 8px solid #d9a9a4;
          padding: 5px 0px;
        }

        input:focus,
        textarea:focus,
        select:focus {
          outline: none;
        }
      }
    }

    .f-menu-c {
      width: 55%;
      align-self: flex-end;
      border-top: 1px solid #d4938b;
      margin-right: 26.5px;
      .f-menu {
        height: 60px;
        padding-right: 8vw;
        ul {
          white-space: nowrap;
          li {
            margin-left: 50px;
            margin-bottom: 23.5px;
          }
        }
      }
    }
  }

}
