%animated,
.animated {
  transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

.app {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: flex;
  min-height: 100vh;
  max-width: 100vw;
  background: #e8f5f6;
  .sidebar {
    @extend %animated;
    display: flex;
    flex-direction: column;
    width: 300px;
    color: #fff;
    background: rgb(33, 37, 67);
    background: linear-gradient(
      90deg,
      rgba(33, 37, 67, 1) 0%,
      rgba(54, 55, 100, 1) 50%,
      rgba(63, 64, 117, 1) 100%
    );
    .logo {
      align-self: center;
      width: calc(100% - 72px);
      margin-top: 39px;
      margin-bottom: 69px;
    }
    ul {
      li {
        display: flex;
        height: 60px;
        align-items: center;
        font-size: 14px;
        padding-left: 12px;
        cursor: pointer;
        .icon {
          height: 50%;
          width: 15%;
          margin-right: 9px;
          display: flex;
          align-items: center;
          justify-content: flex-end;
          img {
            float: right;
          }
        }
      }

      li.active {
        width: calc(calc(100% + 3px) - 12px);
        background: #d4938b;
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px;
        flex-direction: row-reverse;
        .icon {
          margin-right: 23px;
        }
      }
    }
  }
  .sidebar.closed {
    @extend %animated;
    width: 70px !important;
    .logo {
      width: 50px;
      height: 60px;
      object-fit: cover;
      object-position: 4% 100%;
    }
    li {
      padding-left: 0px;
      p {
        display: none;
      }
      img {
        float: unset !important;
        width: 50%;
      }
      .icon {
        display: flex;
        width: 100%;
        justify-content: center;
        margin-right: 0px;
      }
    }
    li.active {
      flex-direction: row;
      width: calc(100% + 3px);
      .icon {
        margin-right: 0px;
      }
    }
  }

  .wrapper {
    width: 100%;
    .navbar {
      height: 53px;
      background: #fff;
      display: flex;
      align-items: center;
      box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
      justify-content: space-between;
      .burger {
        cursor: pointer;
        margin-left: 5px;
      }
      .icons {
        display: flex;
        align-items: center;
        margin-right: 60px;
        img{
          margin-left: 30px;
          cursor: pointer;
        }
      }
    }
    .c-main {
      padding: 31px 39px;
    
    }
  }
}
